import React from "react";
import BrandTable from "./table";
import PostBrand from "./post";
import { useState } from "react";
import { BrandDelete, BrandsGet } from "../../redux/brands";
import { useDispatch } from "react-redux";
import PutBrand from "./put";

function BrandsComponent({ open, handleClose }) {
  const dispatch = useDispatch();
  const [adminId, setAdminId] = useState();
  const [openDelete, setOpenDelete] = useState(false);
  const handleCloseDelete = () => setOpenDelete(false);

  const handleDeleteModal = (e) => {
    setOpenDelete(true);
  };

  const HandleDelete = async (e) => {
    await dispatch(BrandDelete(e.currentTarget.id));
    dispatch(BrandsGet());
    handleCloseDelete();
  };

  const [openPut, setOpenPut] = useState(false);

  const onClosePut = () => {
    setOpenPut(false);
  };

  const handlePutModal = async (e) => {
    setOpenPut(true);
    setAdminId(e.currentTarget.id);
  };
  return (
    <>
      <BrandTable
        onClickDelete={handleDeleteModal}
        HandleDelete={HandleDelete}
        handlePutModal={handlePutModal}
      />
      <PostBrand Open={open} HandleClose={handleClose} />
      <PutBrand put_id={adminId} open={openPut} HandleClose={onClosePut} />
    </>
  );
}

export default BrandsComponent;
