import * as React from "react";
import { useSelector } from "react-redux";
import TableCommon from "../../common/table";
import "./styles.css";
import { Image, Popover, Space } from "antd";

export default function BrandTable({ HandleDelete, handlePutModal }) {
  const brandsGetState = useSelector((state) => state.brand);
  const rows = brandsGetState.brandsGet?.data;

  const data = [];
  rows?.map((elem, index) => {
    data.push({
      data: index + 1,
      key: elem.id,
      Фото: (
        <Image
          width={60}
          height="100%"
          style={{
            aspectRatio: "1 / 1",
            borderRadius: "20px",
            objectFit: "cover",
          }}
          src={elem.image}
        />
      ),
      Название: elem.brands_name_ru,
      Страна: elem.country,
      Удалить: (
        <div className="boxx">
          <div className="btn-wraps">
            <button onClick={handlePutModal} id={elem.id}>
              <i id={elem.id} class="bx bx-message-square-edit"></i>
            </button>
          </div>
          <Space wrap>
            <Popover
              trigger="click"
              placement="rightBottom"
              content={
                <div className="content_delete_box">
                  <p>Вы уверены, что хотите удалить эту админ?</p>
                  <div className="btn_wrap_delete">
                    <button
                      onClick={HandleDelete}
                      id={elem.id}
                      className="yes_btn"
                    >
                      Удалить
                    </button>
                  </div>
                </div>
              }
              title={
                <div className="delete_box">
                  <i class="bx bxs-error-circle"></i>
                  <span>
                    Удалите админ <span>{elem.name}</span>
                  </span>
                </div>
              }
            >
              <div className="btn-wrap">
                <button id={elem.id}>
                  <i class="bx bxs-trash"></i>
                </button>
              </div>
            </Popover>
          </Space>
        </div>
      ),
    });
  });

  const columns = [
    {
      title: "№",
      dataIndex: "data",
      key: "data",
      width: 50,
      align: "center",
    },
    {
      title: "Фото",
      dataIndex: "Фото",
      key: "Фото",
    },
    {
      title: "Название",
      dataIndex: "Название",
      key: "Название",
    },
    {
      title: "Страна",
      dataIndex: "Страна",
      key: "Страна",
    },
    {
      title: "Удалить",
      dataIndex: "Удалить",
      key: "Удалить",
      align: "center",
    },
  ];
  return (
    <TableCommon
      bordered
      columns={columns}
      data={data}
      pagination={false}
      scroll={{
        y: 330,
      }}
    />
  );
}
