import React, { useEffect, useState } from "react";
import HeaderTopCommon from "../../components/common/HeaderTop";
import { WrapperContainer } from "../../style-App";
import { useDispatch } from "react-redux";
import HorizontalSidebar from "../../components/horizontal-sidebar";
import VerticalSidebar from "../../components/vertical-sidebar";
import BrandsComponent from "../../components/brands";
import { BrandsGet } from "../../redux/brands";

function Brands() {
  const [open, setOpen] = useState(false);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(BrandsGet());
  }, []);

  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);

  return window.localStorage.getItem("checked") == "false" ? (
    <HorizontalSidebar>
      <WrapperContainer style={{ marginTop: "112px" }}>
        <HeaderTopCommon
          title={"Бренды"}
          onClick={handleOpen}
          textBtn={"Добавить бренд"}
        />
        <BrandsComponent handleClose={handleClose} open={open} />
      </WrapperContainer>
    </HorizontalSidebar>
  ) : (
    <VerticalSidebar>
      <WrapperContainer>
        <HeaderTopCommon
          title={"Бренды"}
          onClick={handleOpen}
          textBtn={"Добавить бренд"}
        />
        <BrandsComponent handleClose={handleClose} open={open} />
      </WrapperContainer>
    </VerticalSidebar>
  );
}
export default Brands;
